interface MapCache {
  map: any;
  view: any;
  defaultId: string;
  [mapId: string]: any;
}

const wellKnownMapIds = {
  topo:1,streets:1,satellite:1,hybrid:1,gray:1,oceans:1,osm:1,terrain:1,
  'national-geographic':1,'dark-gray':1,'dark-gray-vector':1,'gray-vector':1,
  'streets-vector':1,'streets-night-vector':1,'streets-navigation-vector':1,
  'topo-vector':1,'streets-relief-vector':1
};

export interface MapProperties {
  mapId?: (keyof typeof wellKnownMapIds) | string;

  center?: LatLng;
  zoom?: number;
}

const icache = createICacheMiddleware<MapCache>();
const factory = create({ i18n, theme, icache }).properties<MapProperties>()

export default factory(function lMap({ /*children,*/ properties, middleware: { /*i18n,*/ theme, icache } }) {
	// const { messages } = i18n.localize(bundle);
	const themedCss = theme.classes(css);


  const switchMap = (id: string = 'fae788aa91e54244b161b59725dcbb2a') => () => {
    loadModules(['esri/Map', 'esri/Basemap', 'esri/WebMap', 'esri/layers/WebTileLayer'])
      .then(([_Map, BaseMap, WebMap, WebTileLayer]) => {
        let map;
        if (typeof id === 'string' && id.substr(0,5) === 'https') {
          console.log(id);
          map = new _Map({
            basemap: new BaseMap({
              baseLayers: [
                new WebTileLayer({
                  urlTemplate: id,
                  title: "Basemap"
                })
              ],
              title: "basemap",
              id: "basemap"
            }),
            ground: "world-elevation"
          });
        } else if (wellKnownMapIds.hasOwnProperty(id)) {
          map = new BaseMap({ basemap: id });
        } else if (/^[a-z|A-Z|0-9]{32}$/.test(id)) {
          map = icache.getOrSet(id, new WebMap({ portalItem: { id } }));
        }
        icache.get('view').map = map;
        icache.set('map', map)
      })
  }

  const createMap = (): any => {
  	const node = document.createElement('div');
  	node.style.height = '100%'; node.style.width = '100%';

  	const { mapId = 'fae788aa91e54244b161b59725dcbb2a', ...options } = properties();
  	if (!Math.max(0,options.zoom||0)) { options.zoom = 14 }
    icache.getOrSet('defaultId', mapId);
  	return dom({
  		node,
  		onAttach: () => {
        loadModules(['esri/views/MapView', 'esri/Map', 'esri/WebMap'])
        .then(([MapView, _Map, WebMap]) => {
          // then we load a * web map * from an id
          const map = icache.getOrSet('map', new WebMap({
            portalItem: { // autocasts as new PortalItem()
              id: mapId // 'fae788aa91e54244b161b59725dcbb2a'
            }
          }));
          // then we load a map from an id
          // const map = new MMap({ basemap: "osm" });
          // and we show that map in a container w/ id #viewDiv
          const view = icache.getOrSet('view', new MapView({
            map,
            container: node,
            center: [-118.71511,34.09042],
            zoom: 11,
            navigation: {
              mouseWheelZoomEnabled: false,
              browserTouchPanEnabled: false
            },
            constraints: { }
          }));
        })
        .catch(err => {
          // handle any errors
          console.error(err);
        });
  		}
  	})
  }
















	// const { center } = properties();
	/*
	const actionItems = ACTION.map((k) => {
		const label = m.hasOwnProperty(`see_${k}`) ? m[`see_${k}`] : k;
		return { label, value: k }
	});
	*/
	// const el = ElementMeta.get('mapcontainer')
  /*
  osm           fae788aa91e54244b161b59725dcbb2a
  imagery meta  c03a526d94704bfb839445e80de95495
  imagery       da10cf4ba254469caf8016cd66369157
  streets       55ebf90799fa4a3fa57562700a68c405
  topo          67372ff42cd145319639a99152b15bc3
  nav           c50de463235e4161b206d000587af18b

  /^[a-z|A-Z|0-9]{32}$/.test()
  */

	return (
		<div classes={[themedCss.root]}>
      <div classes={[themedCss.mapSwitch]}>
        <i onclick={switchMap(icache.get('defaultId'))}>X</i>
        <i onclick={switchMap('fae788aa91e54244b161b59725dcbb2a')}>OSM</i>
        <i onclick={switchMap('da10cf4ba254469caf8016cd66369157')}>SAT</i>
        <i onclick={switchMap('55ebf90799fa4a3fa57562700a68c405')}>STR</i>
        <i onclick={switchMap('67372ff42cd145319639a99152b15bc3')}>TOP</i>
        <i onclick={switchMap('https://tiles.wmflabs.org/hikebike/{level}/{col}/{row}.png')}>6</i>
      </div>
      {createMap()}
    </div>
	);
});
